"use client";

import { useState } from "react";
import { Menu, X } from "lucide-react";

export default function Navbar({ activeSection, setActiveSection }) {
  const [isOpen, setIsOpen] = useState(false);

  const navItems = [
    { label: "Home", id: "home" },
    { label: "Cultural Points", id: "points" },
    { label: "Events", id: "events" },
    { label: "Gallery", id: "gallery" },
    { label: "Team", id: "team" },
  ];

  const handleNavClick = (id) => {
    setActiveSection(id);
    setIsOpen(false);
  };

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 navbar-glass border-b border-white/10">
      <div className="max-w-7xl mx-auto px-2 sm:px-4 lg:px-8">
        {/* Row: logo at far left, nav pushed to the right */}
        <div className="flex items-center h-16 sm:h-20 w-full">
          {/* LOGO: keep left, reduce internal gap so it's closer to the very left */}
          <div className="flex items-center gap-2 text-white font-bold carnival-font drop-shadow-lg flex-none">
            <img
              src="/main-logo.webp"
              alt="INSIGHT Logo"
              className="h-14 sm:h-16 w-auto drop-shadow-lg hover:scale-105 transition-transform"
            />
            <span className="text-lg sm:text-2xl font-cinzel">INSIGHT 25–26</span>
          </div>

          {/* Spacer pushes nav to the far right */}
          <div className="flex-1" />

          {/* NAV: aligned to the far right */}
          <div className="flex items-center justify-end">
            {/* Desktop Navigation */}
            <div className="hidden md:flex gap-10 items-center pr-4">
              {navItems.map((item) => (
                <button
                  key={item.id}
                  onClick={() => handleNavClick(item.id)}
                  className={`cursor-pointer transition-all duration-300 font-semibold text-base sm:text-lg drop-shadow-md ${
                    activeSection === item.id ? "text-accent scale-110" : "text-white hover:text-accent"
                  }`}
                >
                  {item.label}
                </button>
              ))}
            </div>

            {/* Mobile Menu Button */}
            <div className="md:hidden">
              <button className="text-white p-2 drop-shadow-lg" onClick={() => setIsOpen(!isOpen)}>
                {isOpen ? <X size={24} /> : <Menu size={24} />}
              </button>
            </div>
          </div>
        </div>

        {/* Mobile Navigation (collapsible) */}
        {isOpen && (
          <div className="md:hidden pb-4 space-y-2 border-t border-white/10 navbar-glass-mobile">
            {navItems.map((item) => (
              <button
                key={item.id}
                onClick={() => handleNavClick(item.id)}
                className={`block w-full text-left px-4 py-2 rounded transition-all ${
                  activeSection === item.id ? "bg-accent text-primary font-bold" : "text-white hover:bg-white/10"
                }`}
              >
                {item.label}
              </button>
            ))}
          </div>
        )}
      </div>
    </nav>
  );
}
