"use client"

import { useState } from "react"
import { eventsData } from "../data/events"
import EventModal from "./EventModal"

export default function EventsTimeline() {
  const [selectedEvent, setSelectedEvent] = useState(null)

  return (
    <section className="py-16 sm:py-20 px-4 bg-transparent min-h-screen">
      <div className="max-w-6xl mx-auto">
        {/* Title */}
        <h2 className="text-4xl sm:text-5xl font-bold text-primary text-center mb-12 carnival-font">Events Timeline</h2>

        {/* Timeline */}
        <div className="space-y-12">
          {Object.entries(eventsData).map((dayEntry) => (
            <div key={dayEntry[0]}>
              <h3 className="text-2xl font-bold text-center text-accent mb-8">{dayEntry[0]}</h3>

              <div className="space-y-6">
                {dayEntry[1].map((event, idx) => (
                  <div
                    key={event.id}
                    className={`flex ${idx % 2 === 0 ? "flex-row" : "flex-row-reverse"} gap-4 md:gap-8 items-stretch`}
                  >
                    {/* Timeline Connector */}
                    <div className="hidden md:flex flex-col items-center w-16">
                      <div className="w-4 h-4 bg-accent rounded-full border-4 border-white shadow-lg"></div>
                      {idx < dayEntry[1].length - 1 && (
                        <div className="w-1 flex-grow bg-gradient-to-b from-accent to-primary mt-2"></div>
                      )}
                    </div>

                    {/* Event Card */}
                    <button
                      onClick={() => setSelectedEvent(event)}
                      className="flex-1 bg-gradient-to-br from-primary-light to-secondary rounded-lg shadow-lg p-6 hover:shadow-xl transform hover:-translate-y-2 transition-all cursor-pointer group text-left"
                    >
                      <div className="flex gap-4">
                        {/* Event Image */}
                        <div className="w-24 h-24 sm:w-32 sm:h-32 flex-shrink-0 bg-gray-200 rounded-lg overflow-hidden">
                          <img
                            src={event.poster || "/placeholder.svg"}
                            alt={event.name}
                            className="w-full h-full object-cover group-hover:scale-110 transition-transform"
                            onError={(e) => {
                              e.target.src = `/placeholder.svg?height=128&width=128&query=${event.name}`
                            }}
                          />
                        </div>

                        {/* Event Details */}
                        <div className="flex-1 text-white">
                          <h4 className="text-lg sm:text-xl font-bold mb-2 group-hover:text-accent transition-colors">
                            {event.name}
                          </h4>
                          <p className="text-sm text-gray-100 mb-2">{event.time}</p>
                          <p className="text-sm text-gray-100 mb-2">{event.location}</p>
                          <p className="text-sm italic text-accent font-semibold">{event.tagline}</p>
                        </div>
                      </div>
                    </button>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Modal */}
      {selectedEvent && <EventModal event={selectedEvent} onClose={() => setSelectedEvent(null)} />}
    </section>
  )
}
