import { departmentsData } from "../data/departments"

export default function CulturalPointsTable() {
  const sortedDepts = [...departmentsData].sort((a, b) => b.totalPoints - a.totalPoints)

  return (
    <section className="py-16 sm:py-20 px-4 bg-transparent">
      <div className="max-w-6xl mx-auto">
        {/* Title */}
        <h2 className="text-4xl sm:text-5xl font-bold text-primary text-center mb-12 carnival-font">
          Cultural Points Table
        </h2>

        {/* Table */}
        <div className="overflow-x-auto shadow-lg rounded-lg">
          <table className="w-full border-collapse">
            <thead>
              <tr className="bg-gradient-to-r from-primary to-secondary text-white">
                <th className="px-4 py-4 text-left font-bold">Rank</th>
                <th className="px-4 py-4 text-left font-bold">Department</th>
                <th className="px-4 py-4 text-center font-bold">Sports Points</th>
                <th className="px-4 py-4 text-center font-bold">Cultural Points</th>
                <th className="px-4 py-4 text-center font-bold">Total Points</th>
              </tr>
            </thead>
            <tbody>
              {sortedDepts.map((dept, idx) => (
                <tr
                  key={dept.id}
                  className={`${
                    idx % 2 === 0 ? "bg-white" : "bg-gray-100"
                  } hover:bg-accent/10 transition-colors border-b border-gray-200`}
                >
                  <td className="px-4 py-4">
                    <span className="inline-flex items-center justify-center w-8 h-8 bg-accent text-primary font-bold rounded-full">
                      {idx + 1}
                    </span>
                  </td>
                  <td className="px-4 py-4 font-semibold text-gray-800">{dept.name}</td>
                  <td className="px-4 py-4 text-center text-gray-700">{dept.sportsPoints}</td>
                  <td className="px-4 py-4 text-center text-gray-700">{dept.culturalPoints}</td>
                  <td className="px-4 py-4 text-center font-bold text-primary">{dept.totalPoints}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </section>
  )
}
