"use client"

import { useEffect, useState } from "react"

export default function Hero() {
  const [countdown, setCountdown] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  })

  useEffect(() => {
    const updateCountdown = () => {
      const targetDate = new Date("December 20, 2025 09:00").getTime()
      const now = new Date().getTime()
      const distance = targetDate - now

      if (distance > 0) {
        setCountdown({
          days: Math.floor(distance / (1000 * 60 * 60 * 24)),
          hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((distance % (1000 * 60)) / 1000),
        })
      }
    }

    updateCountdown()
    const interval = setInterval(updateCountdown, 1000)
    return () => clearInterval(interval)
  }, [])

  const scrollToEvents = () => {
    document.getElementById("events")?.scrollIntoView({ behavior: "smooth" })
  }

  return (
    <section className="relative min-h-screen w-full flex flex-col items-center justify-center px-4 overflow-hidden">
      {/* INSIGHT image + college name: plain (no glass) */}
      <div className="relative z-10 flex flex-col items-center mb-6">
        <img
          src="/Insight-name.webp"
          alt="INSIGHT Event Logo"
          className="w-76 sm:w-80 md:w-120 h-auto mb-2 mx-auto drop-shadow-[0_6px_20px_rgba(0,0,0,0.6)]"
        />
        <p className="mt-1 sm:mt-2 px-4 text-sm sm:text-lg md:text-xl font-cinzel font-semibold text-[#F8D48A] drop-shadow-[0_2px_10px_rgba(0,0,0,0.65)]">
          St. Vincent Pallotti College of Engineering and Technology, Nagpur
        </p>
      </div>

      {/* Countdown: glass morphism cards with golden/carnival glow.
          These styles affect countdown cards only and won't make the hero background dark. */}
      <div className="relative z-10 w-full m-2 max-w-4xl">
        <div className="grid grid-cols-4 gap-2 sm:gap-4 mt-2 mb-12 justify-center">
          {[
            { value: countdown.days, label: "Days" },
            { value: countdown.hours, label: "Hours" },
            { value: countdown.minutes, label: "Minutes" },
            { value: countdown.seconds, label: "Seconds" },
          ].map((item, idx) => (
            <div key={idx} className="relative group">
              <div className="absolute inset-0 bg-gradient-to-br from-yellow-400 via-amber-300 to-orange-400 rounded-xl blur-lg opacity-60 group-hover:opacity-100 transition-opacity duration-300 animate-pulse-glow"></div>

              {/* Glass morphism card with carnival gradient */}
              <div className="relative bg-gradient-to-br from-purple-500/30 via-pink-500/20 to-red-500/30 backdrop-blur-xl border border-white/20 rounded-xl p-3 sm:p-6 min-w-16 sm:min-w-24 transform group-hover:scale-110 transition-all duration-300 shadow-2xl">
                {/* Inner light effect */}
                <div className="absolute inset-0 bg-gradient-to-t from-transparent via-white/10 to-white/20 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>

                {/* Content */}
                <div className="relative z-10">
                  <div className="text-2xl sm:text-4xl font-bold bg-gradient-to-r from-yellow-300 via-amber-200 to-yellow-200 bg-clip-text text-transparent">
                    {String(item.value).padStart(2, "0")}
                  </div>
                  <div className="text-xs sm:text-sm font-semibold text-white/90 mt-1 drop-shadow-md">{item.label}</div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
  
    </section>
  )
}
